



























#app {
height: 100%;
}

html,body {
margin: 0;
padding: 0;
border: 0;
height: 100%;
max-height: 100%;
position: relative;
}

#toolbar {
  float: left;
  width: 80px;
  height: 100%;
  background-color: #505050;
  color: #fff;
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  /* No support for these yet, use at own risk */
  -o-user-select: none;
  user-select: none;
}

#toolbar img {
  font-size: 40px;
  cursor: pointer;
  margin: 40px 20px 20px 20px;
}

#toolbar img:hover
{
  border:1px solid #000;
}
  
.starred {
  border:1px solid #000;
}

.material-icons {
display: inline-block;
line-height: 1;

-webkit-filter: invert(1);
-moz-filter: invert(1);
-o-filter: invert(1);
-ms-filter: invert(1);
filter: invert(1); 
}



