
























#note-editor {
  height: 100%;
  margin-left: 380px;
}

textarea, #note-editor div {
  display: inline-block;
  width: 100%;
  height: 50%;
  vertical-align: top;
  box-sizing: border-box;
  padding: 0 20px;
	overflow: auto;
}

textarea {
  height: 50%;
  border: 0;
  border-radius: 0;
  border-top: 1px solid #B8B8B8;
  resize: none;
  outline: none;
  background-color: #F0F0F0;
  font-size: 20px;
  padding: 20px;
}

code {
  color: #f66;
}




