
































































#notes-list {
  font-family:"Microsoft YaHei";
  float: left;
  width: 300px;
  height: 100%;
  background-color: #888888;
}

#list-button {
  text-align:center;
}

#list-button div{
  display:inline;
}

#list-header h2 {
  text-align: center;
  font-size: 30px;
  color: #fff;
}

#list-header button {
  font-family:"Microsoft YaHei";
  border-radius:5px;
  font-size: 20px;
  cursor: pointer;
  color: #fff;
  width:120px;
  outline: none;
}

button:hover
{
  border:2px solid #fff;
}

.container{
  height: calc(100% - 150px);
	max-height: calc(100% - 150px);
	overflow: auto;
}

.list-group-item {
  cursor: pointer;
  text-align: center;
  line-height: 40px;
  height: 40px;
  font-size: 20px;
  color:#fff;
}

.list-group-item:hover{
  border:1px solid #fff;
}

.active {
  background-color:#505050;
}



